@media screen and (max-width: 1600px) {
}

@media screen and (min-width: 1200px) {
  .restaurant-item .restaurant-item__header .restaurant-item__header__poster {
    width: auto;
    height: 18rem;
  }

  .error__container {
    width: 40%;
  }
}

@media screen and (max-width: 1200px) {
