* {
  margin: 0;
  padding: 0;
}

body {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.skip-to-content {
  position: fixed;
  margin: 10px;
  padding: .5rem;
  text-align: center;
  text-decoration: none;
  color: black;
  z-index: 101;
  background-color: yellowgreen;
  translate: -200px 0;
}

.skip-to-content:focus {
  translate: 0;
}


/*
  AppBar
*/

.nav-bar {
  padding: 6px 32px;
