adding: 10px 15px;\n  cursor: pointer;\n}\n\n.nav-bar .nav-bar__brand {\n  min-width: 44px;\n  min-height: 44px;\n  font-size: 1.5rem;\n  display: flex;\n  align-items: center;\n}\n\n.nav-bar .nav-bar__brand p {\n  display: flex;\n  align-items: center;\n}\n\n.nav-bar .nav-bar__brand a {\n  cursor: pointer;\n}\n\n.nav-bar .nav-bar__brand .firstname {\n  display: flex;\n  align-items: center;\n  min-width: 44px;\n  min-height: 44px;\n  color: #f6f6f6;\n  text-decoration: none;\n}\n\n.nav-bar .nav-bar__brand .lastname {\n  display: flex;\n  align-items: center;\n  min-width: 44px;\n  min-height: 44px;\n  color: rgb(0, 213, 0);\n  text-decoration: none;\n}\n\n.nav-bar .nav-bar__navigation {\n  display: flex;\n  justify-content: start;\n  align-items: center;\n  position: absolute;\n  left: -180px;\n  top: 47px;\n  width: 150px;\n  transition: all 0.3s;\n  background-color: #222;\n  overflow: hidden;\n  border-bottom-right-radius: 20px;\n  padding-right: 50px;\n}\n\n.nav-bar .nav-bar__navigation.open {\n  left: 0px;\n  position: fixed;\n  box-shadow: 0 8px 4px rgba(0, 0, 0, 0.444);\n}\n\n.nav-bar .nav-bar__navigation ul {\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  justify-content: center;\n  gap: .5rem;\n}\n\n.nav-bar .nav-bar__navigation ul li {\n  list-style-type: none;\n  border-radius: 12px;\n}\n\n.nav-bar ul li:hover {\n  background-color: #303030;\n}\n\n.nav-bar .nav-bar__navigation ul li a {\n  text-decoration: none;\n  color: #f6f6f6;\n  font-size: 1.1rem;\n  font-family: \"Open Sans\", sans-serif;\n  height: 44px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n\n/* \n  Hero\n*/\n\n.hero {\n  width: 100%;\n  height: 100vh;\n  color: white;\n  font-family: \"Open Sans\", sans-serif;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  background-image: url('../public/hero-image_4.jpg');\n  background-position: top;\n  background-size: cover;\n}\n\n.hero .hero-description {\n  text-shadow: 0 2px 4px black;\n  line-height: 4rem;\n  font-size: 4rem;\n}\n\n\n/* \n  Main Content\n*/\n \nmain {\n  padding: 32px;\n  flex: 1;\n  background-color: #f6f6f6;\n}\n\n.content {\n  margin: 0 auto;\n  min-height: 100%;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 16px;\n}\n\n.content .content__heading {\n  color: #252525;\n  font-family: \"Open Sans\", sans-serif;\n  font-size: 1.8rem;\n  padding: 20px 0 10px 0;\n}\n \n\n/* \n  Restaurants\n*/\n\n.restaurant {\n  width: 80%;\n  display: grid;\n  grid-template-columns: repeat(3, 1fr);\n  gap: 16px;\n  margin: 0;\n  font-family: \"Open Sans\", sans-serif;\n}\n\n\n/*\n  Detail Restaurant\n*/\n\n.restaurant.grid__edit {\n  display: grid;\n  grid-template-columns: 1fr;\n  margin: 0 auto;\n}\n\n.restaurant.grid__edit .content__heading {\n  margin: 0 auto;\n  color: #252525;\n  font-family: \"Open Sans\", sans-serif;\n  font-size: 1.8rem;\n  padding: 20px 0 10px 0;\n}\n\n.restaurant.grid__edit .restaurant__container {\n  display: flex;\n  justify-content: center;\n  gap: 30px;\n} \n\n.restaurant.grid__edit .restaurant__container .restaurant__picture {\n  width: 20rem;\n  height: 25rem;\n  object-fit: cover;\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.25);\n}\n\n.restaurant.grid__edit .restaurant__container .restaurant__text__container {\n  width: 50%;\n  color: #252525;\n}\n\n.restaurant.grid__edit .restaurant__container .restaurant__text__container .restaurant__description {\n  margin-top: 20px;\n}\n\n.restaurant.grid__edit .restaurant__container .restaurant__text__container .restaurant__name {\n  padding-bottom: 10px;\n}\n\n.restaurant.grid__edit .restaurant__info {\n  margin: 30px auto;\n  color: #f6f6f6;\n  width: 100%;\n  padding: 15px 0;\n  background-color: #292929;\n  border-radius: 12px;\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.25);\n}\n\n.restaurant.grid__edit .restaurant__info .restaurant__info__all__menu {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  margin: 10px 0 15px;\n  gap: 30px 0;\n}\n\n.restaurant.grid__edit .restaurant__info .restaurant__info__all__menu .detail__label__menu {\n  text-align: center;\n  color: rgb(108, 255, 108);\n  margin-bottom: 30px;\n}\n\n.restaurant.grid__edit .restaurant__info .restaurant__info__all__menu .detail__menu {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.restaurant.grid__edit .restaurant__info .restaurant__info__all__menu .detail__menu ul {\n  display: grid;\n  grid-template-columns: repeat(2, 1fr);\n  width: 70%;\n  height: fit-content;\n  row-gap: 15px;\n  color: #e4e4e4;\n  text-align: center;\n}\n\n.restaurant.grid__edit .restaurant__info .restaurant__info__all__menu .detail__menu ul li {\n  list-style-type: none;\n}\n\n.restaurant.grid__edit .restaurant__info .restaurant__info__all__menu .detail__menu div {\n  margin: 0 auto;\n  width: 80%;\n  padding: 5px;\n}\n\n\n/* \n  Customers Review\n*/\n\n.restaurant.grid__edit .customer__review {\n  display: grid;\n  gap: 16px;\n}\n\n.restaurant.grid__edit .customer__review .content__heading {\n  color: #252525;\n  font-family: \"Open Sans\", sans-serif;\n  font-size: 1.8rem;\n  padding: 20px 0 10px 0;\n}\n\n.restaurant.grid__edit .customer__review .customer__review__container {\n  display: grid;\n  grid-template-columns: repeat(4, 1fr);\n  gap: 20px 25px;\n  padding: 0 50px;\n}\n\n.restaurant.grid__edit \n.customer__review \n.customer__review__container\n.customer__review__card__container {\n  border-radius: 8px;\n  box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.15);\n}\n\n.restaurant.grid__edit \n.customer__review \n.customer__review__container\n.customer__review__card__container \n.customer__review__item {\n  padding: 20px;\n  color: #252525;\n}\n\n.restaurant.grid__edit \n.customer__review \n.customer__review__container\n.customer__review__card__container \n.customer__review__item\n.review__name {\n  font-weight: 700;\n}\n\n.restaurant.grid__edit \n.customer__review \n.customer__review__c